import React from "react";
import PromotionAnimation from "./promotionAnimation";
import Enroll from "./Enroll";

const Promotion = () => {
  return (
    <div>
      <div className="promotion_wrapper" style={{ background: "#fff" }}>
        <div className="container">
          <PromotionAnimation />
          <Enroll />
        </div>
      </div>
    </div>
  );
};

export default Promotion;
