import React, { Component } from "react";
import { firebaseMatches } from "../../../firebase";
import { firebaseLooper, reverseArray } from "../../ui/misc";

import MatchesBlock from "../../ui/matches_block";
import Slide from "react-reveal/Slide";

export default class Blocks extends Component {
  state = {
    matches: []
  };

  componentDidMount = () => {
    firebaseMatches
      .limitToLast(6)
      .once("value")
      .then((snapshot) => {
        const matches = firebaseLooper(snapshot);

        const reversMatches = matches.reverse(); // reverse array with list matches like filtered by date

        this.setState({
          matches: reversMatches
        });
        
      });
  };

  showMatches = (matches) =>
    matches
      ? matches.map((match) => (
          <Slide key={match.id} bottom>
            <div className="item" >
              <MatchesBlock match={match} />
            </div>
          </Slide>
        ))
      : null;

  render() {
    return (
      <div className="home_matches">{this.showMatches(this.state.matches)}</div>
    );
  }
}
