import React from 'react'
import { CityLogo } from "../ui/utils";

const Footer = () => {
  return (
    <footer className="bck_blue">
      <div className="footer_logo">
      <CityLogo 
              linkTo='/' 
              width='70px'
              height='70px'
              link={true} />
      </div>
      <div className="footer_discl">
      Manchester city 2018. All rights reserved.
      </div>
    </footer>
  )
}

export default Footer
