import React, { Component } from "react";
import Layout from "./Hoc/layout";
import { Switch, Route } from "react-router-dom";

import PrivateRoute from "./components/authRoutes/privateRoutes";
import PublicRoute from "./components/authRoutes/publicRoutes";

import Home from "./components/home";
import SignIn from "./components/signin";
import Team from "./components/team";
import Matches from "./components/matches";
import NotFound from "./components/ui/notFound";


import Dasboard from "./components/admin/Dashboard";
import AdminMatches from "./components/admin/matches";
import AddMatch from "./components/admin/matches/addMatches";
import AdminPlayers from "./components/admin/players";
import AddPlayers from "./components/admin/players/addPlayers";

const Routes = (props) => {
  return (
    <Layout>
      <Switch>
        <PrivateRoute {...props} path="/dashboard" exact component={Dasboard} />
        <PrivateRoute {...props} path="/admin_matches" exact component={AdminMatches} />
        <PrivateRoute {...props} path="/admin_players" exact component={AdminPlayers} />
        <PrivateRoute {...props} path="/admin_matches/edit_match" exact component={AddMatch} />
        <PrivateRoute {...props} path="/admin_matches/edit_match/:id" exact component={AddMatch} />
         <PrivateRoute {...props} path="/admin_players/add_players/:id" exact component={AddPlayers} />
        <PrivateRoute {...props} path="/admin_players/add_players" exact component={AddPlayers} />

        <PublicRoute {...props}  restricted={false} path="/" exact component={Home} />
        <PublicRoute {...props}  restricted={true} path="/sign_in" exact component={SignIn} />
        <PublicRoute {...props}  restricted={false} path="/the_team" exact component={Team} />
        <PublicRoute {...props}  restricted={false} path="/the_matches" exact component={Matches} />
        <PublicRoute {...props}  restricted={false}  component={NotFound} />

      </Switch>
    </Layout>
  );
};

export default Routes;
